:root{
  --primary_background: #3f325c;
  --secondary_background: #f9f9f9;
  --primary_button_color: #bb9cd8;
  --secondary_button_color: #9b5aac;
  --button_shadow: #a09797;
  --text_primary_accent: #F9F9F9;
  --text_secondary_accent: #192A51;
  --notfound_primary_text: #9379c0;
  --notfound_secondary_text: #7c69a0;
  --about_border: #281e3d;
  --aboutJohn_primary_color: #bb9cd8;
  --aboutMainChar_color: #120b20BB;
  --aboutNyan_primary_color: #f59dd0;
  --aboutNyan_secondary_color: #770d36;
  --aboutWoofer_primary_color: #f8b689;
  --aboutWoofer_secondary_color: #a7453e;
  --aboutHops_primary_color: #f3e389;
  --aboutHops_secondary_color: #b3761a;
  --stage_primary_color: #24152e;
  --stage_secondary_color: #120b20;
}

.resize-animation-stopper * {
  animation: none !important;
  transition: none !important;
}

*{
  font-family: Arial;
  color: var(--text_secondary_accent);
  max-width: 100%;
  margin: 0;
}
html{
  
  background-color: var(--primary_background);
}
.body{
  background-color: var(--primary_background);
  padding-bottom: 100px;
}
.body{
  -ms-overflow-style: none;  /* Internet Explorer 10+ */
  scrollbar-width: none;  /* Firefox */}
*::-webkit-scrollbar{display: none;  /* Safari and Chrome */}

/*404 PAGE:*/

.notfound{
  margin: 15% 0 0 0;
  align-items: center;
  text-align: center;
  animation: 2s lineUp ease-out;
  }
  @keyframes lineUp {
    0% {
      opacity: 0;
      transform: translateY(80%);
    }
    20% {
      opacity: 0;
    }
    50% {
      opacity: 1;
      transform: translateY(0%);
    }
    100% {
      opacity: 1;
      transform: translateY(0%);
    }
  }

.notfound h1{
  padding: 30px;
  color: var(--notfound_primary_text);
  font-size: 60px;
}

.notfound p{
  font-size: 30px;
  margin: 0 0 30px 0;
  color: var(--notfound_secondary_text);
}

.notfound a{
  padding: 5px;
  border-radius: 16px;
  background-color: var(--primary_button_color);
  text-decoration: none;
  font-size: 25px;
  font-weight: bold;
  color: var(--text_secondary_accent);
}

.notfound a:hover{
  background-color: var(--secondary_background);
  box-shadow: 2px 2px 5px var(--primary_button_color);
}

/*HOME BANNER*/

.home img{
  position: relative;
  left:0;
  background-image: url("./img//BG0.gif");
  width: 100%;
  border-bottom: 4px solid var(--secondary_background);
}

.home h2{
  text-align: center;
  color: var(--secondary_background);
  font-size: 50px;
  font-family: cursive;
  font-weight: bold;
}

/*NAVBAR:*/

.navbar{
  padding-top: 1em;
  background-color: var(--secondary_background);
  text-align: center;
  position: relative;
  width: 100%;
  padding-bottom: 0.1em;
  z-index: 999;
}

.Logo{
  margin: 0.5rem;
}

.Pages{
  position: absolute;
  text-align: left;
  top: 100%;
  left: 0;
  background-color: var(--secondary_background);
  width: 100%;
  transform: scale(1,0);
  transform-origin: top;
  transition: transform 400ms ease-in-out;
}

.Pages ul{
  margin: 0;
  padding: 0;
  list-style: none;
}

.Pages a{
  display: flex;
  padding-bottom: 0.75em;
  padding-top: 0.75em;
  padding-left: 25px;
  text-decoration: none;
  font-size: 1.2rem;
  font-weight: bold;
  table-layout:fixed;
  opacity: 0;
  transition: opacity 200ms ease-in-out;
  overflow-x: clip;
}
.Pages li:has(> a.active){
  background-color: var(--primary_button_color);
}
.Pages a.active{
  color:var(--text_primary_accent);
}

.navbar_toggle{display: none;}
.navbar_toggle:checked ~ .Pages{
  transform: scale(1,1);
}
.navbar_toggle_label{
  position: absolute;
  top: 0;
  left: 0;
  margin-left: 0.5em;
  height: 100%;
  display: grid;
  align-items: center;
}
.menu_button{
  grid-column: 1;
  grid-row: 1;
  margin: 1/2;
  height: 2em;
  width: 2em;
  position: relative;
  opacity:1;
  transition: opacity 200ms ease-in-out;
}

.close_button{
  grid-column: 1;
  grid-row: 1;
  height: 1.7em;
  width: 1.7em;
  position: relative;
  opacity:0;
  transition: opacity 200ms ease-in-out;
}
.navbar_toggle:checked ~ label .close_button{
  display: block;
  opacity:1;
  transition: opacity 200ms ease-out 50ms;
}
.navbar_toggle:checked ~ label .menu_button{
  opacity:0;
  transition: opacity 200ms ease-in-out;
}
.navbar_toggle:checked ~ .Pages a{
  opacity: 1;
  transition: opacity 250ms ease-in-out 250ms;
}

@media screen and (min-width: 800px) {
  .navbar_toggle_label{display: none;}
  .Pages{
    all: unset;
    display: flex;
    justify-content: center;
  }
  .Pages a{
    display: inline;
    margin-left: 0;
    opacity: 1;
    font-weight: bold;
    padding: 0.6em;
  }
  .Pages ul{
    margin: 1em 0; 
  }
  .Pages li{
    all:unset;
    margin: 0.8rem;
  }
  .Pages li:has(> a.active){
    background-color: inherit;
  }
  .Pages a.active{
    color: var(--text_primary_accent);
    font-weight: bold;
    background-color: var(--primary_button_color);
    box-shadow: 2px 2px 5px var(--button_shadow);
    border-radius: 2rem;
  }
  .Pages a:hover{
    color: var(--text_primary_accent);
    font-weight: bold;
    background-color: var(--secondary_button_color);
    box-shadow: 2px 2px 5px var(--button_shadow);
    border-radius: 2rem;
    transition: background-color 250ms;
  }
}

body div h2{
  color:var(--text_primary_accent);
  text-align: center;
} 

/*FOOTER:*/

.footer{
  padding-top: 1%;
  position: fixed;
  align-items: center;
  width: 100%;
  margin: 0 auto;
  background-color: var(--secondary_background);
  border-top: 3px solid var(--primary_button_color) ;
  white-space: nowrap;
  left: 0;
  bottom: 0;
}

.footer p{
  background-color: inherit;
  text-align: center;
  margin: 5px 0 0 0;
  color: #682e60;
}

.footer .links{
  background-color: inherit;
  display: block;
}

.footer .links ul{
  background-color: inherit;
  text-align: center;
  padding:0 0 0 0;
}

.footer .links ul li{
  display: inline-block;
  list-style-type: none;
  background-color: inherit;
  padding:2px;
  white-space: nowrap;
}

.footer .links ul li a{
  background-color: inherit;
}

.footer .links ul li a img{
  width: 40px;
  padding:5px;
  background-color: inherit;
}

.footer .links ul li a img:hover{
  border-radius: 20px;
  background-color: var(--primary_button_color);
  box-shadow: 2px 2px 5px var(--primary_background);
  transition: background-color 250ms, box-shadow 250ms;
}

@media screen and (max-width:300px) {
  .footer p{
    font-size: small;
  }  
}

